.side-drawer{
    height: 100%;
    background: goldenrod;
    box-shadow: 2px 0px 5px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width:25rem;
    z-index: 200;
    /* transform: translateX(-100%);
    transition: transform 0.3s ease-out; */
}
.side-drawer.open{
    transform:translateX(0) ;
}
.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li{
    margin: 0.5rem 0;
}

.side-drawer a{
    color: blue;
    text-decoration: none;
}

.side-drawer a:hover,
.side-drawer a:active{
    color: deeppink;
}

@media (min-width: 769px){
    .side-drawer{
        display: none;
    }
}